    background: #ff5a1d;
    border-color: #ff5a1d;
    box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.15); }

.gradient {
  position: relative; }
  .gradient:after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: inline-block;
    z-index: 1;
    background: linear-gradient(to bottom, rgba(31, 31, 41, 0.2), rgba(31, 31, 41, 0.8)); }

