@import "variabler";

body{
    background: $off-white;
}

h1,h2,h3 {
  font-family: $inter-font;
  margin-bottom: 0;
  font-weight: 900;
}

p {
  font-family: $roboto-font;
  font-weight: 300;
  line-height: 1.6;
}

a{
    text-decoration: none;
    color: $off-white;
}
.wrapper{
    max-width: 1400px;
    padding: 1rem 2rem;
    margin: 0 auto;
}

.highlight{
    color: $orange;
}

svg{
    color: $orange;
    stroke-width: 1;
}

hr{
    width: 80px;
    border: 2px solid $orange;
    background: $orange;
}
.btn{
border: 3px solid $orange;
cursor: pointer;
text-align: center;
display: inline-block;
text-transform: uppercase;
padding: 1em 3.5em;
border-radius: 50px;
font-family: $inter-font;
color: $orange;
font-size: 14px;
font-weight: 900;
transition: 300ms;

@media screen and (max-width: 992px){
  display: block;
}

&:hover{
color: $off-white;
background: $orange;
}
&:active{
  color: $off-white;
  background: darken($orange, 10%);
  border-color: darken($orange, 10%);
  box-shadow: 0px 4px 16px rgba($color: #000000, $alpha: 0.15);

  
}
}

.gradient{
  position: relative;

  &:after {
    content:'';
    position:absolute;
    left:0; top:0;
    width:100%; height:100%;
    display:inline-block;
    z-index: 1;
    background: linear-gradient(to bottom, rgba($navy, 0.2), rgba($navy, 0.8));
  }
}
/* Navbar / Intro Section */
.navbar{
    font-family: $inter-font;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 1em 0;
    
    @media screen and (max-width: 992px){
        flex-wrap: wrap;
    }

    .logo{
        font-family: $inter-font;
        font-weight: 900;
        font-size: 24px;
    }

    .nav-menu{
        cursor: pointer;
        display: none;

        svg{
        color: $off-white;
        width: 28px;
        height: auto;
    }

        @media screen and (max-width: 992px){
            display: flex;
        }
    }

    .nav-links{
        list-style: none;
        display: flex;
        text-align: right;
        ;

        @media screen and (max-width: 992px){
            display: none;
            width: 100%
        }

        li{
            padding: 0;
            margin: 0 1em;

            @media screen and (max-width: 992px) {
                margin: 0.7em 0.5em;
            }
        }
        .nav-link{
            color: $off-white;
            font-family: $roboto-font;
            font-weight: 300;
            font-size: 15px;
            text-transform: uppercase;
            opacity: 0.7;
            transition: 200ms ease-in-out;

            &:hover{
                opacity: 1;
            }

            svg{
                color: white;
                width: 13px;
                height: 13px;
            }
        }
        .active{
            font-weight: 400;
            opacity: 1;
        }
    }
    .dropdown-menu{
        display: none;
        position: absolute;

        @media screen and (max-width: 992px) {
            position: relative;
        }


        .dropdown-content{
            display: flex;
            flex-direction: column;
            padding: 1em 0;

            .dropdown-link{
                color: rgba($color: $off-white, $alpha: 0.7);
                font-size: 13px;
                text-transform: uppercase;
                display: block;
                border-left: 1px solid rgba($color: $off-white, $alpha: 0.5);
                padding: 0.6em 1em;
                transition: 200ms ease-in-out;

                @media screen and (max-width: 992px) {
                    padding: 0.6em 1em;
                    border-left: none;
                    border-right: 1px solid rgba($color: $off-white, $alpha: 0.5);
                    font-size: 15px;
                }

                &:hover{
                    color: $off-white;
                    border-color: $off-white;
                }

            }
        }

        
    }


}

.intro-section, .subheader{
  background: linear-gradient(to bottom, rgba($navy, 0.9), rgba($navy, 0.9)), url('../img/bg-pattern.jpg') center center;
  background-size: cover;
    color: $off-white;
}



.intro-content{
    padding: 6em 0 8em;
    max-width: 600px;

    h1{
        font-size: 56px;
    }
    p{
        line-height: 1.4;
        font-size: 18px;
    }

}

.subheader{
    .intro-content{
        text-align: center;
        max-width: 100%;

        h1{
            font-size: 56px;
            text-transform: uppercase;
        }
    
    }
}
/* Feature section */
.front-features{
    padding: 3em 0;
    background: $white;

    .wrapper{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        padding: 4em 0;
        @media screen and (max-width: 992px) {
            grid-template-columns: 1fr;
            padding: 0em 2em;
        }

        div{
            flex-direction: column;
            text-align: center;

            .feature-overskrift{
                text-transform: uppercase;
                font-family: $inter-font;
                font-weight: 900;
                font-size: 16px;
            }
            .feature-lead{
                font-size: 16px;
                font-weight: 300;
                padding: 0 1.5em;
            }
        }
    }


    svg{
        width: 70px;
        height: auto;
    }
}
/* Ydelser - section */
.ydelser-section{
    background: $navy;
    padding: 3em 0;
    color: $off-white;
    position: relative;
    z-index: 0;

    .wrapper{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        column-gap: 50px;
        padding: 4em 0;

        @media screen and (max-width: 992px) {
          padding: 0em 2em;
          grid-template-columns: 1fr;
        }
    }
    h2{
        text-transform: uppercase;
        font-size: 1.75em;
    }
    .ydelses-billede{
            transition: 200ms ease-in-out;
            box-shadow: 0px 0px 20px -8px rgba($color: #000000, $alpha: 0.4);

        h3{
          position: absolute;
          z-index: 10;
          bottom: 15px;
          left: 15px;
          text-transform: uppercase;
        }

        &:hover {
          transform: scale(0.99);
          transition: 200ms ease-in-out;
          box-shadow: 0px 0px 20px -8px rgba($color: $orange, $alpha: 0.4);
        }
    }
    img{
        max-width: 100%;
        margin-top: 1.5em;
        height: auto;
        z-index: 1;
        display:block;
        position: relative;

    }
}

/* Møde section */
.mode-section{
  background: $white;
  padding: 3em 0;

  .wrapper{
    display: grid;
    grid-template-columns: 1fr 1.5fr;
    column-gap: 50px;
    max-width: 1200px;
    padding: 4em 0;

    @media screen and (max-width: 992px) {
      padding: 0em 2em;
      grid-template-columns: none;
      grid-template-rows: auto;
      row-gap: 50px;
    }
  }

  img{
    max-width: 100%;
  }
  hr{
    margin: auto 0;
  }
  h2{
    font-size: 1.75em;
    text-transform: uppercase;
  }
  p{
    max-width: 640px;
  }
}



/* Ydelser - Stephanie */
.webudvikling, .markedsføring, .hosting {
  .wrapper {
    hr {
      margin: 0;
    }
    .segment {
      p {
        margin-bottom: 40px;
      }
    }
  }

  .image-webudvikling {
    background: url("../img/udvikling.jpg") center center;
    background-size: cover;
    height: 340px;
    margin-bottom: 30px;
  }

  .image-analyse {
    background: url("../img/analyse.jpg")  center center;
    background-size: cover;
    height: 340px;
    margin-bottom: 30px;
  }
}

.webudvikling {
  .wrapper {
    padding-top: 3rem;
  }
}

@media screen and (min-width: 992px) {
    .webudvikling, .markedsføring, .hosting {
        .wrapper {
          display: grid;
          grid-template-columns: repeat(3, 1fr);
          grid-column-gap: 50px;
          margin: 50px auto;

          hr {
            margin-bottom: 10px;
          }

            .segment {
                grid-column: (1);

              p {
                margin-bottom: 0;
              }
            }

            .segment:nth-of-type(2) {
                grid-column: (2);

              p {
                margin-bottom: 0;
              }
            }

            .segment:nth-of-type(3) {
                grid-column: (3);

              p {
                margin-bottom: 0;
              }
            }
        }

      .image-webudvikling {
        background: url("../img/udvikling.jpg") center center;
        background-size: cover;
        height: 400px;
        margin-bottom: 35px;
      }

      .image-analyse {
        background: url("../img/analyse.jpg")  center -350px;
        background-size: cover;
        height: 400px;
        margin-bottom: 35px;
      }
    }

  .webudvikling {
    .wrapper {
      padding-top: 1rem;
    }
  }
}






