}
.kontaktmap {
  background: $white;
  .wrapper {
    .maptekst {
      text-align: center;
      padding-top: 20px;
      svg {
        color: $orange;
        width: 40px;
        height: auto;
      }
      p {
        font-family: $inter-font;
        font-weight: 900;
        font-size: 22px;
        margin-top: 5px;
      }
    }
  }
}
