  .kontakt {
    .wrapper {
      display: flex;
      flex-direction: row;

      .kontakttekst {
        width: 50%;
        p {
          padding-right: 100px;
          padding-top: 15px;

        }
      }
      .kontaktinfo {
        .kontaktinfo-item {
          display: flex;
          margin-top: -25px;

          svg {
            padding-top: 32px;
          }

          p {
            padding-left: 12px;
          }
        }
      }
      .kontaktformular {
        width: 50%;
        padding-top: 30px;
      }
    }
  }
}
/* Footer Stephanie */
.footer {
  background: $navy;
  color: $off-white;
  padding-top: 3em;

  .wrapper {
    .logo {
      font-family: $inter-font;
      font-weight: 900;
      font-size: 24px;
    }

    p {
      margin-bottom: 20px;
      max-width: 600px;
    }

    .footer-wrapper {
      display: flex;
      margin-bottom: 20px;

      p {
        margin: 0 0 0 20px;
      }
    }

    svg {
      color: $off-white;
    }
  }

    .some-footer {
      background: darken($navy, 4);
      margin-top: 2em;

      .wrapper{
        display: flex;
        justify-content: space-between;
        padding: 0.25em 3em;
      }

      .copyright-box {
        .copyright {
          font-size: 12px;
        }
      }

      .some-box {
        svg {
          width: 15px;
          height: auto;
          padding: 0 5px;
          margin-top: 0.8em;
          color: $off-white;
        }
      }
    }
}

@media screen and (min-width: 992px) {
  .footer {
    .wrapper {
      display: grid;
      grid-template-columns: repeat(4, 1fr);

      .footer-logo {
        grid-column: 1/span 3;

        .logo {
          font-size: 28px;
        }
      }

      .footer-info {
        grid-column: (4);
        margin-top: 25px;

        .footer-wrapper {
          justify-content: flex-end;
        }
      }
    }
  }
}